import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)
import Xue from '@/components/index.js'

const router = new Router({
    routes: [{
            path: '/',
            redirect: '/index'
        },
        {
            path: '/index',
            components: {
                default: () => import('@/views/Index.vue')
            }
        },
        {
            path: '/introduction',
            components: {
                header: () => import('@/views/common/header.vue'),
                navbar: () => import('@/views/common/navbar.vue'),
                default: () => import('@/views/Introduction.vue')
            }
        },
        {
            path: '/install',
            components: {
                header: () => import('@/views/common/header.vue'),
                navbar: () => import('@/views/common/navbar.vue'),
                default: () => import('@/views/Install.vue')
            }
        }, {
            path: '/start',
            components: {
                header: () => import('@/views/common/header.vue'),
                navbar: () => import('@/views/common/navbar.vue'),
                default: () => import('@/views/Start.vue')
            }
        },
        {
            path: '/components',
            components: {
                header: () => import('@/views/common/header.vue'),
                navbar: () => import('@/views/common/navbar.vue'),
                default: () => import('@/views/Components.vue')
            },
            children: [{
                    path: 'basic/:component?',
                    component: () => import('@/views/components/Basic.vue')
                }, {
                    path: 'navigation/:component?',
                    component: () => import('@/views/components/Navigation.vue')
                },
                {
                    path: 'form/:component?',
                    component: () => import('@/views/components/Form.vue')
                },
                {
                    path: 'layout/:component?',
                    component: () => import('@/views/components/Layout.vue')
                },
                {
                    path: 'viewport/:component?',
                    component: () => import('@/views/components/Viewport.vue')
                },
                {
                    path: 'others/:component?',
                    component: () => import('@/views/components/Others.vue')
                }
            ]
        }
    ]
})

router.beforeEach((to, from, next) => {
    const pattern = /^(\/\w+)(?:(\/\w+)\/\w+)?$/
    to.path.match(pattern)
    let to1 = RegExp.$1
    let to2 = RegExp.$2
    from.path.match(pattern)
    let from1 = RegExp.$1
    let from2 = RegExp.$2
    if (to1 !== from1 || to2 !== from2) {
        Xue.Loading.start()
    }
    if (/^\/\w+$/.test(to.path)) {
        window.scrollTo(0, 0)
    }
    next()
})

router.afterEach(() => {
    Xue.Loading.bar && Xue.Loading.finish()
})

export default router