class Formator {
    constructor() {}

    relativeTime(data) {
        if (!data) { return '' }
        let dateObj
        if (typeof data === 'string' || typeof data === 'number') {
            dateObj = new Date(data)
        } else if (typeof data === 'object') {
            dateObj = data
        } else { return '' }
        let now = Date.now()
        let time = dateObj.getTime()
        let space = (now - time) / 1000 //
        if (space < 60) { return '' }
        if (space < 60 * 60) {
            return Math.floor(space / 60) + ''
        }
        if (space < 60 * 60 * 24) {
            return Math.floor(space / (60 * 60)) + ''
        } else {
            return Math.floor(space / (60 * 60 * 24)) + ''
        }
    }

    timeStr(params) {
        let data
        if (typeof params === 'string') {
            data = new Date(params)
        } else {
            data = params
        }
        let year = data.getFullYear()
        let month = this.fixed(data.getMonth() + 1)
        let date = this.fixed(data.getDate())
        let day = this.switchDay(data.getDay())
        let hour = this.fixed(data.getHours())
        let minutes = this.fixed(data.getMinutes())
        let seconds = this.fixed(data.getSeconds())
        return { date: `${year}-${month}-${date}`, time: `${hour}:${minutes}:${seconds}`, day }
    }

    fixed(params) {
        if (params > 9) {
            return params
        } else {
            return `0${params}`
        }
    }

    switchDay(params) {
        return ['', '', '', '', '', '', ''][params]
    }
}

export default Formator