import Icon from './basic/icon/index.js'
import Button from './basic/button/index.js'
import Group from './basic/group/index.js'
import Switch from './form/switch/index.js'
import Input from './form/input/index.js'
import Radio from './form/radio/index.js'
import Cascader from './form/cascader/index.js'
import DatePicker from './form/datepicker/index.js'
import TimePicker from './form/timepicker/index.js'
import Grid from './layout/grid/index.js'
import Container from './layout/container/index.js'
import Collapse from './layout/collapse/index.js'
import Waterfall from './layout/waterfall/index.js'
import Sticky from './navigation/sticky/index.js'
import Tabs from './navigation/tabs/index.js'
import Menu from './navigation/menu/index.js'
import Pager from './navigation/pager/index.js'
import Loading from './navigation/loading/index.js'
import Steps from './navigation/steps/index.js'
import Popover from './viewport/popover/index.js'
import Message from './viewport/message/index.js'
import Slides from './viewport/slides/index.js'
import Table from './viewport/table/index.js'
import Spread from './others/spread/index.js'
import Spin from './others/spin/index.js'
import Scroll from './others/scroll/index.js'
import Formator from './others/formator/index.js'

const components = [
    Icon,
    Button,
    Group,
    Switch,
    Input,
    Radio,
    Cascader,
    DatePicker,
    TimePicker,
    Grid,
    Container,
    Collapse,
    Waterfall,
    Sticky,
    Tabs,
    Pager,
    Steps,
    Menu,
    Popover,
    Slides,
    Table,
    Spread,
    Spin,
    Scroll
]

const install = Vue => {
    components.forEach(component => {
        component.install(Vue)
    })
    Vue.use(Message)
    Vue.use(Loading)
}

export { Message, Loading, Formator }
export default { install, Loading, Message, Formator }