<template>
    <div class="linker">
        <div class="back">
            <template v-if="back">
                <x-icon name="left" class="icon"></x-icon>
                <a href="javascript:void(0)" @click="onBack">{{back.text}}</a>
            </template>
        </div>
        <div class="to">
            <template v-if="to">
                <a href="javascript:void(0)" @click="onTo">{{to.text}}</a>
                <x-icon name="right" class="icon"></x-icon>
            </template>
        </div>
    </div>
</template>
<script>
    export default {
        name: 'Linker',
        inject: ['eventBus'],
        props: {
            back: Object,
            to: Object
        },
        methods: {
            onBack() {
                this.eventBus.$emit('link-to', this.back.path)
            },
            onTo() {
                this.eventBus.$emit('link-to', this.to.path)
            }
        }
    }
</script>
<style scoped lang="scss">
    @import '@/assets/color.scss';
    .linker {
        width: 100%;
        display: flex;
        border-top: $borderbase;
        margin-top: 120px;
        >div {
            width: 50%;
            display: flex;
            justify-content: flex-start;
            align-items: center;
            color: $p;
            padding: 20px 0;
            >a {
                color: $p;
                margin-left: 10px;
            }
            &:hover {
                >.icon {
                    transition: all .3s linear;
                    transform: translateX(-3px);
                }
            }
            &:last-child {
                justify-content: flex-end;
                >a {
                    margin-right: 10px;
                }
                &:hover {
                    >.icon {
                        transform: translateX(3px);
                    }
                }
            }
        }
    }
</style>